@font-face {
  font-family: "PT Mono";
  src: url("../fonts/PTMono-Regular.woff2") format("woff2"), url("../fonts/PTMono-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Bold.woff2") format("woff2"), url("../fonts/RobotoCondensed-Bold.woff") format("woff");
  font-weight: 700;
  font-display: swap;
}
@font-face {
  font-family: "Roboto Condensed";
  src: url("../fonts/RobotoCondensed-Regular.woff2") format("woff2"), url("../fonts/RobotoCondensed-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
}
@font-face {
  font-family: "Roboto";
  src: url("../fonts/Roboto-Medium.woff2") format("woff2"), url("../fonts/Roboto-Medium.woff") format("woff");
  font-weight: 500;
  font-display: swap;
}
html {
  box-sizing: border-box;
  overflow-y: scroll;
  scroll-behavior: smooth;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "PT Mono", "Arial", monospace;
  font-style: normal;
  min-width: 320px;
  min-height: 100vh;
  display: grid;
  grid-template-rows: auto 1fr auto;
}

.page__body-lock {
  height: 100vh;
  overflow: hidden;
}

a {
  text-decoration: none;
  color: #ffffff;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

img {
  max-width: 100%;
  height: auto;
  vertical-align: middle;
}

.title {
  font-size: 36px;
  line-height: 50px;
  font-weight: 400;
  margin-top: 0;
  font-family: "Roboto Condensed", "Arial", sans-serif;
}
@media (max-width: 1023px) {
  .title {
    font-size: 32px;
    line-height: 45px;
  }
}
@media (max-width: 767px) {
  .title {
    font-size: 28px;
    line-height: 39px;
  }
}

.button {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  font-size: 14px;
  line-height: 16px;
  letter-spacing: 0.05em;
  color: #ffffff;
  background-color: transparent;
  border: 1px solid #ffffff;
  padding: 14px;
  border-radius: 5px;
  text-transform: uppercase;
  font-weight: 700;
  cursor: pointer;
}
.button:hover {
  background-color: #00b2ff;
  border: none;
}

.wrapper {
  max-width: 1460px;
  padding: 0 15px;
  margin: 0 auto;
}
@media (max-width: 1023px) {
  .wrapper {
    padding: 0 25px;
  }
}
@media (max-width: 767px) {
  .wrapper {
    padding: 0 16px;
  }
}

.container {
  max-width: 1200px;
  margin: 0 auto;
}

.overflow {
  overflow: hidden;
}

.header {
  background-color: #1b2d37;
  background-image: url("../img/bg@1x.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  color: #ffffff;
  padding-top: 34px;
  padding-bottom: 275px;
  z-index: 0;
  overflow: hidden;
}
@media (min-resolution: 2dppx) {
  .header {
    background-image: url("../img/bg@2x.jpg");
  }
}
@media (max-width: 1023px) {
  .header {
    padding-top: 20px;
    padding-bottom: 163px;
  }
}
@media (max-width: 767px) {
  .header {
    padding-bottom: 71px;
    padding-top: 17px;
  }
}

.header__info {
  margin-left: 4px;
  position: relative;
  z-index: 1;
}
@media (max-width: 1023px) {
  .header__info {
    margin-left: 0;
  }
}

.header__info h1 {
  font-family: "Roboto Condensed", "Arial", sans-serif;
  text-transform: uppercase;
  font-weight: 700;
  font-size: 80px;
  line-height: 94px;
  letter-spacing: 0.07em;
  margin: 0;
  margin-bottom: 26px;
}
@media (max-width: 1023px) {
  .header__info h1 {
    font-size: 60px;
    line-height: 70px;
    max-width: 470px;
    margin-left: 63px;
    margin-bottom: 28px;
  }
}
@media (max-width: 767px) {
  .header__info h1 {
    font-size: 40px;
    line-height: 47px;
    margin-top: 230px;
    margin-left: 0;
    margin-bottom: 27px;
  }
}

.header__info p {
  font-size: 22px;
  line-height: 34px;
  max-width: 680px;
  margin-left: 98px;
  position: relative;
  margin-bottom: 44px;
}
.header__info p::before {
  content: "";
  width: 66px;
  height: 2px;
  background-color: #ffffff;
  position: absolute;
  top: 17px;
  left: -94px;
}
@media (max-width: 1023px) {
  .header__info p {
    font-size: 18px;
    line-height: 28px;
    max-width: 356px;
    margin-left: 114px;
    margin-bottom: 55px;
  }
  .header__info p::before {
    content: "";
    width: 33px;
    left: -48px;
    top: 16px;
  }
}
@media (max-width: 767px) {
  .header__info p {
    margin-left: 50px;
    font-size: 16px;
    line-height: 25px;
    margin-bottom: 41px;
  }
}

.header__info a {
  background-color: #00b2ff;
  width: 407px;
  border: none;
  font-size: 16px;
  line-height: 19px;
  padding-top: 18px;
  padding-bottom: 18px;
  padding-left: 45px;
  padding-right: 45px;
  margin-left: 98px;
  display: block;
}
.header__info a:hover {
  background-color: transparent;
  outline: 1px solid #ffffff;
}
@media (max-width: 1023px) {
  .header__info a {
    padding-left: 31px;
    padding-right: 31px;
    width: 379px;
    margin-left: 114px;
    text-align: center;
  }
}
@media (max-width: 767px) {
  .header__info a {
    width: auto;
    margin: 0 auto;
    padding-top: 16px;
  }
}

.header__info-wrapper {
  position: absolute;
  top: -59px;
  left: 789px;
  z-index: -1;
  width: 707px;
}
.header__info-wrapper::before {
  position: absolute;
  content: "";
  left: 13px;
  top: 14px;
  z-index: -1;
  background: url("../img/triangles.svg") left -128px top -164px;
  background-repeat: no-repeat;
  width: 611px;
  height: 488px;
}
@media (max-width: 1023px) {
  .header__info-wrapper::before {
    top: -34px;
    left: -61px;
    background-size: 612px auto;
    background-position: left -92px top -127px;
  }
}
@media (max-width: 767px) {
  .header__info-wrapper::before {
    top: -23px;
    left: 138px;
    background-size: 587px auto;
    background-position: left -92px top -124px;
  }
}
@media (max-width: 1023px) {
  .header__info-wrapper {
    position: absolute;
    top: -76px;
    left: 485px;
    z-index: -1;
    width: 465px;
  }
}
@media (max-width: 767px) {
  .header__info-wrapper {
    position: absolute;
    top: -194px;
    left: 9px;
    z-index: -1;
    width: 302px;
  }
}

@media (max-width: 767px) {
  .header__button--desktop {
    display: none;
  }
}

.header__button--mobile {
  display: none;
}
@media (max-width: 767px) {
  .header__button--mobile {
    display: block;
  }
}

