  }
}

.main-nav__button.button {
  margin-left: 50px;
  min-width: 180px;
  background-color: transparent;
  border: 1px solid #ffffff;
  justify-content: center;
}
@media (max-width: 1023px) {
  .main-nav__button.button {
    display: none;
  }
}

.main-nav ul {
  display: flex;
  align-items: center;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin-left: auto;
}

.main-nav li {
  margin-left: 89px;
}
@media (max-width: 1023px) {
