}

.main-nav__list span {
  margin-left: 15px;
}
@media (max-width: 1023px) {
  .main-nav__list span {
    margin: 0;
    display: none;
  }
}

@media (max-width: 767px) {
  .main-nav__logo--desktop {
    display: none;
  }
}

.main-nav__logo--mobile {
  display: none;
}
@media (max-width: 767px) {
  .main-nav__logo--mobile {
    display: block;
  }
}

.company {
  background-image: url("../img/company-triangles.svg");
  background-repeat: no-repeat;
  background-position: center top -29px;
}
@media (max-width: 1023px) {
