  padding-left: 42px;
  padding-right: 42px;
  min-height: 300px;
  border: 1px solid #ebebeb;
  border-left: 3px solid #00b2ff;
  width: calc(50% - 15px);
  margin-bottom: 30px;
  background-color: #ffffff;
}
.benefits li:hover {
  background-color: #00b2ff;
}
.benefits li:hover svg {
  fill: #ffffff;
}
.benefits li:hover h2, .benefits li:hover p {
