.goods h2 {
  margin: 0;
  margin-bottom: 54px;
}
@media (max-width: 1023px) {
  .goods h2 {
    margin-bottom: 50px;
    max-width: 613px;
  }
}
@media (max-width: 767px) {
  .goods h2 {
    margin-bottom: 44px;
    padding-left: 10px;
  }
