.goods__group {
  display: flex;
  column-gap: 30px;
  flex-wrap: wrap;
  row-gap: 30px;
}
@media (max-width: 1023px) {
  .goods__group {
    flex-direction: column;
    row-gap: 0;
  }
}

.goods__wrapper {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}
@media (max-width: 1023px) {
  .goods__wrapper {
    padding-left: 25px;
    padding-right: 24px;
