.goods a {
  font-size: 20px;
  line-height: 30px;
  flex: 1 1 300px;
  position: relative;
  z-index: 1;
  display: block;
  max-width: 370px;
}
.goods a::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  display: block;
  width: 100%;
  height: 100%;
  background: #1b2d37;
  opacity: 0;
}
.goods a:hover::before {
  opacity: 0.5;
}
.goods a:active::before {
  opacity: 0.7;
}
.goods a::after {
  content: "";
  position: absolute;
  display: block;
  z-index: 2;
  bottom: 2px;
  right: 0;
  width: 63px;
  height: 63px;
  background-image: url("../img/arrow-right.svg");
  background-repeat: no-repeat;
  background-color: #00b2ff;
  background-position: center;
}
@media (max-width: 1023px) {
  .goods a {
    width: 100%;
    min-height: 256px;
    max-width: none;
    flex: 1;
    margin-bottom: 47px;
    overflow: hidden;
  }
}
@media (max-width: 767px) {
  .goods a {
    margin-bottom: 30px;
  }
}

.goods span {
  position: absolute;
  top: 50px;
  left: 43px;
