.about {
  padding-top: 74px;
  padding-bottom: 99px;
  color: $about-text;
  z-index: 1;

  @media (max-width: $tablet-width) {
    padding-top: 57px;
    padding-bottom: 72px;
  }

  @media (max-width: $mobile-width) {
    padding: 15px;
    padding-top: 31px;
    padding-right: 10px;
    padding-bottom: 53px;
  }
}

.about h2 {
  font-size: 36px;
  line-height: 50px;
  font-weight: 400;
  font-family: $font-secondary;
  margin: 0;
  margin-bottom: 22px;

  @media (max-width: $tablet-width) {
    font-size: 32px;
    line-height: 45px;
    margin-bottom: 27px;
  }

  @media (max-width: $mobile-width) {
    font-size: 28px;
    line-height: 39px;
    margin-bottom: 22px;
  }
}

.about p {
  font-size: 16px;
  line-height: 21.5px;
  font-family: $font-primary;
  margin: 0;
  max-width: 984px;
  margin-bottom: 22px;

  @media (max-width: $tablet-width) {
    padding-left: 5px;
    margin-bottom: 21px;
  }

  @media (max-width: $mobile-width) {
    padding-left: 0;
    margin-bottom: 44px;
  }
}

.about p:last-of-type {
  margin-bottom: 39px;

  &::after {
    display: none;

    @media (max-width: $tablet-width) {
      content: "..";
      display: inline;
    }
  }

  @media (max-width: $tablet-width) {
    margin-bottom: 43px;
  }

  @media (max-width: $mobile-width) {
    margin-bottom: 30px;
  }
}

.about a {
  color: $basic-blue;
  border-color: $basic-blue;
  text-align: center;
  display: inline-block;
  vertical-align: middle;
  min-width: 180px;
  margin-bottom: 10px;

  &:hover {
    color: $basic-white;
    background-color: $basic-blue;
    border: 1px solid transparent;
  }

  @media (max-width: $tablet-width) {
    margin-left: 5px;
  }

  @media (max-width: $mobile-width) {
    margin-left: 2px;
  }
}

.about span {
  @media (max-width: $tablet-width) {
    display: none;
  }
}

.about__wrapper {
  padding-left: 195px;
  background-image: url("../img/2005@1x.png");
  background-repeat: no-repeat;
  background-position: left -15px top 4px;
  background-size: 220px 317px;


  @media (min-resolution: 2dppx) {
    background-image: url("../img/2005@2x.png");
  }

  @media (max-width: $tablet-width) {
    padding-left: 208px;
    padding-right: 23px;
    background-position: left -8px top 0;
    background-size: 250px;
    min-height: 363px;
  }

  @media (max-width: $mobile-width) {
    padding-left: 0;
    background-image: none;
    padding-right: 0;
  }
}
